import React from 'react';
import InputNumber from 'antd/es/input-number';
import Input from 'antd/es/input';
import Checkbox from 'antd/es/checkbox';
import Select from 'antd/es/select';
import Radio from 'antd/es/radio';
import BEditor from '../rich-editor';
class FormsItem extends React.Component {
    render() {
        const { item, value, onChange, form } = this.props;
        return ((option) => {
            const { unavailable = false, displayName } = option;
            switch (item.editor) {
                case 'normal':
                    return (<Input disabled={unavailable} placeholder={`请输入${displayName}`} 
                    // @ts-ignore
                    value={value} onChange={onChange}/>);
                case 'password':
                    return (<Input disabled={unavailable} type="password" placeholder={`请输入${displayName}`} 
                    // @ts-ignore
                    value={value} onChange={onChange}/>);
                case 'number':
                    return (<InputNumber disabled={unavailable} placeholder={`请输入${displayName}`} 
                    // @ts-ignore
                    value={value} onChange={onChange} min={item.range ? item.range[0] : undefined} max={item.range ? item.range[1] : undefined}/>);
                case 'text':
                    return <div className="ant-form-item-control-wrapper">{value}</div>;
                case 'select':
                    return (<Select disabled={unavailable} value={value} onChange={onChange}>
              {item.opts &&
                        item.opts.map((opt) => (<Select.Option key={opt.id} value={opt.id}>
                    {opt.name}
                  </Select.Option>))}
            </Select>);
                case 'radio':
                    return (<Radio.Group disabled={unavailable} value={value} onChange={onChange}>
              {item.opts &&
                        item.opts.map((opt) => (<Radio key={opt.id} value={opt.id}>
                    {opt.name}
                  </Radio>))}
            </Radio.Group>);
                case 'texarea':
                    return (<Input.TextArea disabled={unavailable} placeholder={`请输入${displayName}`} 
                    // @ts-ignore
                    value={value} onChange={onChange}/>);
                case 'checkbox':
                    return (<Checkbox.Group disabled={unavailable} 
                    // @ts-ignore
                    value={value} onChange={onChange}>
              {item.opts &&
                        item.opts.map((opt) => (<Checkbox key={opt.id} value={opt.id}>
                    {opt.name}
                  </Checkbox>))}
            </Checkbox.Group>);
                /* case "table-select":
                    return <TableSelect disabled={unavailable} item={item} form={form} value={value} onChange={onChange} />
                case "file-upload":
                    return <FileUpLoad disabled={unavailable} item={item} form={form} value={value} onChange={onChange} /> */
                case 'bEditor':
                    return (<BEditor disabled={unavailable} form={form} 
                    // @ts-ignore
                    value={value} onChange={onChange}/>);
                case 'hidden':
                    return (<Input 
                    // @ts-ignore
                    value={value} onChange={onChange}/>);
                default:
                    return (<Input disabled={unavailable} placeholder={`请输入${displayName}`} 
                    // @ts-ignore
                    value={value} onChange={onChange}/>);
            }
        })(item);
    }
}
export default FormsItem;
